.nav {
  min-height: 10vh;
  display: flex;
  z-index: 0;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 2rem;
  h1 {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 2rem;
    color: rgb(61, 61, 61);
  }
  button {
    border: none;
    color: rgb(59, 59, 59);
    border: solid 1px rgb(59, 59, 59);
    padding: 1rem 2rem;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 1.5rem;
    cursor: pointer;
    background: transparent;
    transition: all 0.3s ease;
    z-index: 20;
  }
  .clicked{
    background-color: rgb(59, 59, 59);
    color: white;
  }
  .fa-music {
    padding-left: 1rem;
  }
  @media screen and (max-width: 750px) {
    button{
     padding: 1rem 1rem;
     flex-wrap: nowrap;
     font-size: 1rem;
    }
   }
   
}
@media screen and (max-width: 750px) {
  .hide{
    justify-content: flex-end;
  }
  .hide{
    h1{display: none}
  }
  
 }
