.library{
    position:fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    list-style: none;
    // background-color: rgb(246, 246, 246);
    overflow-y: scroll;
    z-index: 10;
    transform: translateX(-100%);
    background-color: aliceblue;
    h2{
        color: black;
        font-family:Verdana, Geneva, Tahoma, sans-serif;
        font-weight: normal;
        font-size: 2rem;
        padding: 3rem 0rem 2rem 2rem;
    }
}
@media screen and (min-width: 600px) {
   .library{
    width: 20rem;
    box-shadow: 2px 2px 50px grey;
   }
   h2{
    font-size: 1rem;
   }
 }

.library-song{
    cursor: pointer;
    border: none;
    width: 100%;
    display: flex;
    align-items: center;
    img{
        width: 40%;
        padding: 1rem 2rem;
    }
    h2,
    h3{
        color: rgb(93, 93, 93);
        font-family:Verdana, Geneva, Tahoma, sans-serif;
        font-weight: normal;
        padding: 0.5rem;
    }
    h2{
        font-size: 1.5rem;
    }
    h3{
        font-size: 1.2rem;
    }
    &:hover{
        background-color: rgb(195, 195, 195);
    }

    @media screen and (min-width: 500px) {
        h2{
         font-size: 1.3rem;
        }
        h3{
            font-size: 1rem;
        }
      }
}

*{
    scrollbar-width: thin;
    scrollbar-color: rgba(155, 155, 155, 0.5) transparant;
}
*::-webkit-scrollbar{
    width: 5px;
}

*::-webkit-scrollbar-track{
    background: transparent;
}
*::-webkit-scrollbar-thumb{
    background-color:  rgba(155, 155, 155, 0.5);
    border-radius: 20px;
}

.selected{
    background-color: rgb(177, 177, 255);
}

.active-library{
    transform: translateX(0%);
    opacity: 1;
}