.song-info-container {
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  img {
    padding-bottom: 1rem;
    width: 40%;
    border-radius: 50%;
  }
  h2, 
  h3{
    padding: 1rem;
  }
  h2{
    font-size: 2rem;
  }
  h3{
    font-size: 1.5rem;
    color: grey;
  }
}
@media screen and (min-width: 750px) {
  .song-info-container{
    padding-top: 0rem;
    img {
      padding-bottom: 0.5rem;
      max-width: 25%;
      border-radius: 50%;
    }
    h2, 
    h3{
      padding: 0.5rem;
    }
    h2{
      font-size: 1.5rem;
    }
  }
  
}
