* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: rgb(234, 234, 234);
  overflow: hidden
}

@media screen and (min-width: 600px) {
  .App{
    transition: all 0.5s ease;
  }
  .library-active{
    margin-left: 30%;
  }
 
}
@media screen and (min-width: 1000px) {
  .App{
    transition: all 0.5s ease;
  }
  .library-active{
    margin-left: 35%;
  }
}


@import "./control";
@import "./songInfo";
@import "./library";
@import "./nav";