.control-container {
  min-height: 15vh;
  max-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  z-index: 20;
}
.control__time {
  width: 80%;
  display: flex;
  align-items: center;
  input {
    width: 100%;
  }
  p {
    text-align: center;
    font-weight: normal;
    font-size: 1rem;
    padding: 0.5rem;
  }
}
.control__player {
  display: flex;
  width: 50%;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  svg {
    cursor: pointer;
    font-size: 1.3em;
  }
}
@media screen and (min-width: 650px) {
}

input[type="range"] {
  -webkit-appearance: none;
}

input[type="range"]::-webkit-slider-runnable-track {
  width: 300px;
  height: 10px;
  background: #ddd;
  border: none;
  border-radius: 3px;
  padding-bottom: 1rem;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: none;
  height: 25px;
  width: 25px;
  border-radius: 50%;
  background: goldenrod;
  margin-top: -4px;
}

input[type="range"]:focus {
  outline: none;
}

input[type="range"]:focus::-webkit-slider-runnable-track {
  background: #ccc;
}
